<template>
  <v-container
    id="dashboard"
    fluid
    tag="section"
    class="fondo"
  >
    <base-alert />
    <v-img
      :src="require('@/assets/fondoBienvenido1.svg')"
    />
    <v-row>
      <v-col cols="12">
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            Datos básicos
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="establecimientoSalud"
                  label="Establecimiento de salud"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="nombre"
                  label="Nombres y apellidos"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <p class="display-1 gray-text">
                  Sexo
                </p>
                <v-radio-group
                  v-model="sexoGroup"
                  row
                >
                  <v-radio
                    label="Masculino"
                    value="masculino"
                  />
                  <v-radio
                    label="Femenino"
                    value="femenino"
                  />
                </v-radio-group>
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="numeroHoja"
                  label="Número de hoja"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="numeroHistoria"
                  label="Número de historia clinica"
                  outlined
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12">
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Administración</p>
            <v-btn
              right
              absolute
              fab
              class="mt-2"
              color="secondary"
              @click.prevent="agregarFilaAdministracion()"
            >
              <v-icon>
                mdi-plus
              </v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text class="mt-4">
            <tr
              v-for="(item, k) in medicamentosAdministrados"
              :key="k"
            >
              <td>
                <v-text-field
                  v-model="item.nombre"
                  small
                  class="ml-2"
                  label="Nombre"
                  outlined
                />
              </td>
              <td>
                <v-dialog
                  ref="dialog"
                  v-model="item.menu2Hora"
                  :return-value.sync="item.hora"
                  persistent
                  width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="item.hora"
                      label="Seleccione la hora"
                      prepend-icon="mdi-clock-time-four-outline"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    />
                  </template>
                  <v-time-picker
                    v-if="item.menu2Hora"
                    v-model="item.hora"
                    full-width
                  >
                    <v-spacer />
                    <v-btn
                      text
                      color="primary"
                      @click="item.menu2Hora = false"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="$refs.dialog[k].save(item.hora)"
                    >
                      Aceptar
                    </v-btn>
                  </v-time-picker>
                </v-dialog>
              </td>
              <td>
                <v-menu
                  v-model="item.menu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="item.fecha"
                      label="Seleccione la fecha"
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    />
                  </template>
                  <v-date-picker
                    v-model="item.fecha"
                    @input="item.menu2 = false"
                  />
                </v-menu>
              </td>
              <td>
                <v-select
                  v-model="item.funcion"
                  class="ml-2"
                  small
                  label="Función"
                  :items="funcion"
                  outlined
                />
              </td>
              <td
                scope="row"
                class="trashIconContainer"
              >
                <v-btn
                  color="secondary"
                  icon
                  @click.prevent="eliminarFilaAdministracion(k, item)"
                >
                  <v-icon>
                    mdi-delete
                  </v-icon>
                </v-btn>
              </td>
            </tr>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Medicamentos</p>
            <v-btn
              right
              absolute
              fab
              class="mt-2"
              color="secondary"
              :loading="loading"
              :disabled="loading"
              @click.prevent="agregarFila()"
            >
              <v-icon>
                mdi-plus
              </v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text class="mt-4">
            <tr
              v-for="(item, k) in medicamentos"
              :key="k"
            >
              <td>
                <v-text-field
                  v-model="item.nombre"
                  small
                  class="ml-2"
                  label="Nombre"
                  outlined
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.presentacion"
                  class="ml-2"
                  small
                  label="Presentación"
                  outlined
                />
              </td>
              <td>
                <v-select
                  v-model="item.tipoVia"
                  class="ml-2"
                  small
                  label="Tipo vía"
                  :items="itemsPresentacion"
                  item-text="name"
                  item-value="value"
                  outlined
                  @change="cargarVia(k, item)"
                />
                <v-select
                  v-model="item.via"
                  class="ml-2"
                  small
                  label="Vía"
                  item-text="name"
                  item-value="value"
                  :items="cargarVia(k, item)"
                  outlined
                />
              </td>
              <td>
                <v-select
                  v-model="item.frecuencia"
                  class="ml-2"
                  small
                  label="Frecuencia"
                  :items="itemsFrecuencia"
                  outlined
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.dosis"
                  class="ml-2"
                  small
                  placeholder="Ingrese la dosis"
                  outlined
                />
              </td>
              <td
                scope="row"
                class="trashIconContainer"
              >
                <v-btn
                  color="secondary"
                  icon
                  @click.prevent="eliminarFila(k, item)"
                >
                  <v-icon>
                    mdi-delete
                  </v-icon>
                </v-btn>
              </td>
            </tr>
          </v-card-text>
          <v-card-text>
            <v-textarea
              v-model="observaciones"
              outlined
              name="input-7-4"
              label="Observaciones"
            />
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Plan de egreso</p>
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="nombre"
                  label="Nombres y apellidos"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="numeroAfiliacion"
                  label="Número afiliación"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="numeroHc"
                  label="Número HC"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="nombreServicio"
                  label="Nombre del servicio"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-menu
                  v-model="menu2Ingreso"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="dateEgreso"
                      label="Seleccione la fecha de salida"
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    />
                  </template>
                  <v-date-picker
                    v-model="dateEgreso"
                    @input="menu2Ingreso = false"
                  />
                </v-menu>
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <p class="display-1 gray-text">
                  Egreso en:
                </p>
                <v-radio-group
                  v-model="egresoGroup"
                  row
                >
                  <v-radio
                    label="Silla de ruedas"
                    value="sillaRuedas"
                  />
                  <v-radio
                    label="Camilla"
                    value="camilla"
                  />
                  <v-radio
                    label="Caminando"
                    value="caminando"
                  />
                </v-radio-group>
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="diagnosticoEgreso"
                  outlined
                  name="input-7-4"
                  label="Diagnóstico del egreso"
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Instrucciones para el paciente y su familia</p>
            <v-btn
              right
              absolute
              fab
              class="mt-2"
              color="secondary"
              @click.prevent="agregarFilaEgresado()"
            >
              <v-icon>
                mdi-plus
              </v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text class="mt-4">
            <v-alert
              border="top"
              colored-border
              type="info"
              elevation="2"
            >
              La continuidad de su recuperación dependerá de su colaboración con las siguientes recomendaciones.
            </v-alert>
          </v-card-text>
          <v-card-subtitle class="mt-4">
            Medicación a suministrar:
          </v-card-subtitle>
          <v-card-text class="mt-4">
            <tr
              v-for="(item, k) in medicamentosRecuperacion"
              :key="k"
            >
              <td>
                <v-text-field
                  v-model="item.nombre"
                  small
                  class="ml-2"
                  label="Medicamento"
                  outlined
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.dosis"
                  class="ml-2"
                  small
                  label="Dosis"
                  outlined
                />
              </td>
              <td>
                <v-dialog
                  ref="dialog1"
                  v-model="item.menu2Hora"
                  :return-value.sync="item.hora"
                  persistent
                  width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="item.hora"
                      label="Seleccione la hora"
                      prepend-icon="mdi-clock-time-four-outline"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    />
                  </template>
                  <v-time-picker
                    v-if="item.menu2Hora"
                    v-model="item.hora"
                    full-width
                  >
                    <v-spacer />
                    <v-btn
                      text
                      color="primary"
                      @click="item.menu2Hora = false"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="$refs.dialog1[k].save(item.hora)"
                    >
                      Aceptar
                    </v-btn>
                  </v-time-picker>
                </v-dialog>
              </td>
              <td
                scope="row"
                class="trashIconContainer"
              >
                <v-btn
                  color="secondary"
                  icon
                  @click.prevent="eliminarFilaEgresado(k, item)"
                >
                  <v-icon>
                    mdi-delete
                  </v-icon>
                </v-btn>
              </td>
            </tr>
          </v-card-text>
          <v-card-text>
            <v-row>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="dietaPermitida"
                  outlined
                  name="input-7-4"
                  label="Dieta permitida"
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="dietaNoPermitida"
                  outlined
                  name="input-7-4"
                  label="Dieta no permitida"
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="actividades"
                  outlined
                  name="input-7-4"
                  label="Actividades"
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="educacionEspecial"
                  outlined
                  name="input-7-4"
                  label="Educación especial"
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Proxima cita</p>
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-menu
                  v-model="menu2FechaCita"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="dateFechaCita"
                      label="Fecha de proxima cita"
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    />
                  </template>
                  <v-date-picker
                    v-model="dateFechaCita"
                    @input="menu2FechaCita = false"
                  />
                </v-menu>
              </v-col>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-menu
                  ref="menu"
                  v-model="menu2Hora"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="timeCita"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="timeCita"
                      label="Hora de proxima cita"
                      prepend-icon="mdi-clock-time-four-outline"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    />
                  </template>
                  <v-time-picker
                    v-if="menu2Hora"
                    v-model="timeCita"
                    full-width
                    @click:minute="$refs.menu.save(timeCita)"
                  />
                </v-menu>
              </v-col>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-text-field
                  v-model="nombreMedico"
                  label="Nombre del medico"
                  outlined
                />
              </v-col>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-text-field
                  v-model="nombreEnfermera"
                  label="Nombre del enfermero/a responsable"
                  outlined
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Guardar ficha</p>
          </v-card-title>
          <v-card-text>
            <v-alert
              class="mt-4"
              border="bottom"
              colored-border
              type="warning"
              elevation="2"
            >
              Una vez guardada la ficha del paciente, podrá redirigirse a la ficha familiar.
            </v-alert>
          </v-card-text>
          <v-card-actions>
            <v-btn
              color="primary"
              center
              @click.prevent="guardarFicha()"
            >
              Guardar ficha
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { mapActions } from 'vuex'
  export default {
    name: 'DashboardDashboard',

    data () {
      return {
        establecimientoSalud: '',
        nombre: '',
        sexoGroup: null,
        numeroHoja: '',
        numeroHistoria: '',
        observaciones: '',
        numeroAfiliacion: '',
        numeroHc: '',
        nombreServicio: '',
        diagnosticoEgreso: '',
        dietaPermitida: '',
        dietaNoPermitida: '',
        actividades: '',
        educacionEspecial: '',
        nombreMedico: '',
        nombreEnfermera: '',
        dateCita: '',
        timeCita: '',
        egresoGroup: null,
        medicamento: '',
        medicamentosAgregados: [],
        itemsPresentacion: [
          {
            name: 'Vía enteral',
            value: 'viaEnteral',
            elementos: [
              { name: 'Vía oral', value: 'viaOral' },
              { name: 'Vía sublingual', value: 'viaSublingual' },
              { name: 'Vía rectal', value: 'viaRectal' },
            ],
          },
          {
            name: 'Via parenteral',
            value: 'viaParenteral',
            elementos: [
              { name: 'Intradérmica', value: 'intradermica' },
              { name: 'Subcutánea', value: 'subcutanea' },
              { name: 'Intramucular', value: 'intramucular' },
              { name: 'Endovenosa', value: 'endovenosa' },
              { name: 'Intracardíaca', value: 'intracardiaca' },
              { name: 'Intraraquídea', value: 'intraraquidea' },
              { name: 'Intraarticular', value: 'intraarticular' },
            ],
          },
        ],
        funcion: ['Doctor', 'Pediatra', 'Enfermería', 'Especialista'],
        itemsFrecuencia: ['Cada día', 'Cada hora', 'Cada dos horas', 'Cada tres horas', 'Cada cuatro horas', 'Cada cinco horas', 'Cada seis horas', 'Cada siete horas', 'Cada ocho horas', 'Cada nueve horas', 'Cada diez horas', 'Cada once horas', 'Cada doce horas'],
        medicamentos: [{
          nombre: '',
          presentacion: '',
          tipoVia: '',
          via: '',
          vias: [],
          frecuencia: '',
          dosis: '',
        }],
        medicamentosRecuperacion: [{
          nombre: '',
          dosis: '',
          hora: '',
          menu2Hora: false,
        }],
        medicamentosAdministrados: [{
          nombre: '',
          hora: '',
          menu2Hora: false,
          menu2: false,
          fecha: new Date().toISOString().substr(0, 10),
          funcion: '',
        }],
        dateEgreso: new Date().toISOString().substr(0, 10),
        dateFechaCita: new Date().toISOString().substr(0, 10),
        menu2FechaCita: false,
        menu2Hora: false,
        menu2Ingreso: false,
        time: null,
        modal2: false,
        loader: null,
        loading: false,
        loading2: false,
      }
    },
    methods: {
      ...mapActions(['agregarFicha']),
      cargarVia (k, item) {
        // var idx = this.medicamentos.indexOf(item)
        var f = this.itemsPresentacion.find(res => {
          return res.value === item.tipoVia
        })
        var d = { ...f }
        return d.elementos
      },
      agregarMedicamento () {
        this.medicamentosAgregados.push(this.medicamento)
      },
      agregarFila () {
        this.medicamentos.push({
          nombre: '',
          presentacion: '',
          tipoVia: '',
          via: '',
          vias: [],
          frecuencia: '',
          dosis: '',
        })
      },
      eliminarFila (index, medicamento) {
        var idx = this.medicamentos.indexOf(medicamento)
        if (idx > -1) {
          this.medicamentos.splice(idx, 1)
        }
      },
      guardarMedicamento () {
        console.log(JSON.stringify(this.medicamentos))
      },
      agregarFilaEgresado () {
        this.medicamentosRecuperacion.push({
          nombre: '',
          dosis: '',
          hora: '',
        })
      },
      agregarFilaAdministracion () {
        this.medicamentosAdministrados.push({
          nombre: '',
          hora: '',
          menu2Hora: false,
          menu2: false,
          fecha: new Date().toISOString().substr(0, 10),
          funcion: '',
        })
      },
      eliminarFilaAdministracion (index, medicamento) {
        var idx = this.medicamentosAdministrados.indexOf(medicamento)
        if (idx > -1) {
          this.medicamentosAdministrados.splice(idx, 1)
        }
      },
      eliminarFilaEgresado (index, medicamento) {
        var idx = this.medicamentosRecuperacion.indexOf(medicamento)
        if (idx > -1) {
          this.medicamentosRecuperacion.splice(idx, 1)
        }
      },
      guardarFicha () {
        const payload = {
          establecimientoSalud: this.establecimientoSalud,
          nombreYapellido: this.nombre,
          sexo: this.sexoGroup,
          numeroHoja: this.numeroHoja,
          numeroHistoria: this.numeroHistoria,
          administracionMedicamentos: this.medicamentosAdministrados,
          medicamentos: this.medicamentos,
          observaciones: this.observaciones,
          numeroAfiliacion: this.numeroAfiliacion,
          numeroHc: this.numeroHc,
          nombreServicio: this.nombreServicio,
          fechaSalida: this.dateEgreso,
          egresoEn: this.egresoGroup,
          diagnosticoEgreso: this.diagnosticoEgreso,
          medicacionSumunistrada: this.medicamentosRecuperacion,
          dietaPermitida: this.dietaPermitida,
          dietaNoPermitida: this.dietaNoPermitida,
          actividades: this.actividades,
          educacionEspecial: this.educacionEspecial,
          fechaCita: this.dateFechaCita,
          horaCita: this.timeCita,
          nombreMedico: this.nombreMedico,
          nombreEnfermera: this.nombreEnfermera,
        }
        this.agregarFicha(payload)
      },
    },
  }
